.adonitrans-plug #contloader {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.7);
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  flex-direction: column;
  z-index: 9999;
  opacity: 0;
  transform: scale(0);
  transition: all 0.5s;
}
.adonitrans-plug #contloader .text {
  color: #fff;
}
.adonitrans-plug #contloader .loader {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  display: inline-block;
  border-top: 4px solid #FFF;
  border-right: 4px solid transparent;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}
.adonitrans-plug #contloader .loader::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  left: 0;
  top: 0;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border-left: 4px solid #00d6b0;
  border-bottom: 4px solid transparent;
  animation: rotation 0.5s linear infinite reverse;
}
.adonitrans-plug.actloader #contloader {
  transform: scale(1);
  opacity: 1;
}
@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.adonitrans-plug .formplug label {
  width: 100%;
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
  row-gap: 9px;
  font-size: 18px;
  letter-spacing: 1px;
  font-weight: 700;
  text-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
  position: relative;
}
.adonitrans-plug .formplug label input {
  outline: none;
  background-color: #fff;
  color: #2d2d2d;
  border: 1px solid #f1f1f1;
}
.adonitrans-plug .formplug label i {
  position: absolute;
  top: 37px;
  right: 15px;
  color: #313131;
  font-size: 26px;
  text-shadow: none;
  cursor: pointer;
  transition: 0.5s;
}
.adonitrans-plug .formplug label i:hover {
  transform: scale(1.1);
}
.adonitrans-plug .formplug label.error {
  color: red;
  font-size: 13px;
  font-weight: 400;
  margin-top: 4px;
  line-height: 1;
  margin-bottom: 0 !important;
  letter-spacing: 0;
}
.adonitrans-plug .button {
  display: inline-block;
  margin-bottom: 15px;
  background-color: #00d6b0;
  color: #2d2d2d;
  box-shadow: 0 0 11px 2px rgba(0, 0, 0, 0.2);
  font-weight: 700;
  padding: 12px 20px;
  border-radius: 5px;
  border: none;
  line-height: 1.4;
  transition: 0.5s;
}
.adonitrans-plug .button:hover, .adonitrans-plug .button:focus {
  background-color: #00b9d9;
  color: #333;
}
.adonitrans-plug .tarjeta {
  background-color: #f1f1f1;
  padding: 25px;
  border-radius: 10px;
  border: 1 solid #eae9f1;
  display: flex;
  flex-wrap: wrap;
  row-gap: 16px;
  margin-bottom: 20px;
}
.adonitrans-plug .tarjeta .wrap-titulo {
  width: 100%;
}
.adonitrans-plug .tarjeta .wrap-titulo .titulo {
  font-size: 1.4em;
  font-weight: 700;
  color: #222;
  line-height: 1.4;
  margin: 0;
}
.adonitrans-plug .tarjeta .wrap-titulo .subtitulo {
  font-size: 1.2em;
  line-height: 1.4;
  margin: 0;
}
.adonitrans-plug .validapass {
  margin-bottom: 20px;
}
.adonitrans-plug .validapass .subtitle {
  letter-spacing: 0;
  color: #333;
  font-size: 16px;
  font-weight: 700;
  margin-bottom: 12px;
}
.adonitrans-plug .validapass ul {
  color: #96989B;
  font-size: 14px;
  line-height: 1.6;
  list-style: none;
}
.adonitrans-plug .validapass .tachado {
  text-decoration: line-through;
  color: green;
}
.adonitrans-plug .center {
  text-align: center;
}
.adonitrans-plug .adonitrans-tabs {
  display: inline-block;
  width: 100%;
}
.adonitrans-plug .adonitrans-tabs-nav {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}
.adonitrans-plug .adonitrans-tabs-nav li {
  cursor: pointer;
  padding: 10px 20px;
  border: none;
  background-color: #086ACF;
  color: #fff;
  font-weight: bold;
  transition: background-color 0.3s ease;
}
.adonitrans-plug .adonitrans-tabs-nav li.active {
  background-color: #00b9d9;
  color: #fff;
}
.adonitrans-plug .adonitrans-tabs-nav li:hover {
  background-color: #00b9d9;
  color: #fff;
}
.adonitrans-plug .adonitrans-tabs-content {
  padding: 20px;
  background-color: #fff;
}
.adonitrans-plug .adonitrans-tabs-content .tab-content {
  display: none;
}
.adonitrans-plug .adonitrans-tabs-content .tab-content.active {
  display: block;
}
.adonitrans-plug .acordeon .acordeon-item {
  margin-bottom: 10px;
}
.adonitrans-plug .acordeon .acordeon-item .acordeon-header {
  background-color: #00b9d9;
  color: #fff;
  border: none;
  padding: 10px;
  width: 100%;
  text-align: left;
  cursor: pointer;
  font-weight: 700;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.adonitrans-plug .acordeon .acordeon-item .acordeon-body {
  display: none;
  padding: 10px;
  background-color: #f9f9f9;
  border-top: 1px solid #ddd;
}
.adonitrans-plug .acordeon .acordeon-item .campo {
  margin-bottom: 15px;
}
.adonitrans-plug .acordeon .acordeon-item .campo label {
  display: block;
  margin-bottom: 5px;
  font-size: 14px;
}
.adonitrans-plug .acordeon .acordeon-item .campo input {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  font-size: 14px;
  border-radius: 4px;
}
.adonitrans-plug #ajustes-generales div {
  margin-bottom: 15px;
}
.adonitrans-plug #ajustes-generales div label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}
.adonitrans-plug #ajustes-generales div input,
.adonitrans-plug #ajustes-generales div textarea {
  width: 100%;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
  box-sizing: border-box;
}
.adonitrans-plug #ajustes-generales div input:focus,
.adonitrans-plug #ajustes-generales div textarea:focus {
  border-color: #007bff;
  outline: none;
}
.adonitrans-plug #ajustes-generales div textarea {
  resize: vertical;
  min-height: 80px;
}
.adonitrans-plug #ajustes-generales #wrap-franjas-trabajo .franja {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
}
.adonitrans-plug #ajustes-generales #wrap-franjas-trabajo .franja button.remove-franja-row:hover {
  background-color: red;
}
.adonitrans-plug #ajustes-generales #wrap-tarifas-descuentos .row-tarifa {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
}
.adonitrans-plug #ajustes-generales #wrap-tarifas-descuentos .row-tarifa button.remove-tarifa-row:hover {
  background-color: red;
}
.adonitrans-plug #ajustes-generales button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 20px;
}
.adonitrans-plug #ajustes-generales button:hover {
  background-color: #0056b3;
}

body.adonitrans-plug {
  overflow: hidden;
}

#panel-administracion {
  background-color: #f7f6f9;
  display: flex;
  flex-wrap: wrap;
  height: 100vh;
}
#panel-administracion #franja {
  width: 100%;
  max-height: 200px;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  background-color: #086ACF;
  padding: 30px 20px;
}
#panel-administracion #franja .contimg,
#panel-administracion #franja a {
  display: flex;
}
#panel-administracion #franja .contimg img,
#panel-administracion #franja a img {
  transition: all 0.5s;
  max-height: 60px;
}
#panel-administracion #franja .contimg img:hover,
#panel-administracion #franja a img:hover {
  transform: scale(1.1);
}
#panel-administracion #franja .continfo {
  color: #fff;
  text-shadow: 1px 1px black;
  font-weight: 700;
}
#panel-administracion #lateral {
  background-color: #086ACF;
  color: #fff;
  width: 20%;
  padding: 30px 0;
  height: calc(100vh - 120px);
  background-image: linear-gradient(to bottom, #086acf, #0072d4, #007ada, #0082de, #008ae3, #0091e5, #0098e6, #009fe7, #00a6e5, #00ade2, #00b3de, #00b9d9);
}
#panel-administracion #lateral ul {
  list-style: none;
  padding: 0;
}
#panel-administracion #lateral ul li {
  padding: 25px 20px;
  font-size: 17px;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: 500;
  cursor: pointer;
  background-color: transparent;
  color: #fff;
  display: flex;
  align-items: center;
  column-gap: 10px;
  transition: 0.5s;
}
#panel-administracion #lateral ul li a {
  color: #fff;
}
#panel-administracion #lateral ul li:hover, #panel-administracion #lateral ul li.active {
  background-color: #fcd722;
  color: #2d2d2d;
}
#panel-administracion #lateral ul li:hover a, #panel-administracion #lateral ul li.active a {
  color: #2d2d2d;
}
#panel-administracion #informacion {
  width: 80%;
  height: calc(100vh - 120px);
  padding: 20px;
  background-color: #f7f6f9;
  box-shadow: 0 0 27px 0px rgba(0, 0, 0, 0.4);
  overflow: auto;
}
#panel-administracion .wrap-gestion-usuarios {
  width: 100%;
}
#panel-administracion form {
  display: flex;
  flex-wrap: wrap;
  column-gap: 20px;
  row-gap: 25px;
}
#panel-administracion form#ajustes-generales {
  flex-direction: column;
}
#panel-administracion form .wrap {
  width: 100%;
  position: relative;
}
#panel-administracion form .wrap label {
  text-shadow: none;
  margin-bottom: 5px;
}
#panel-administracion form .wrap a {
  color: #086ACF;
  text-decoration: underline;
  transition: all 0.5s;
}
#panel-administracion form .wrap a:hover {
  color: #00b9d9;
}
#panel-administracion form .wrap .select2-container .select2-selection--single {
  height: 42px;
}
#panel-administracion form .wrap .select2-container .select2-selection--single .select2-selection__rendered {
  line-height: 42px;
}
#panel-administracion form .wrap.wrap-2 {
  width: 49%;
}
#panel-administracion form #extra-fields-container,
#panel-administracion form #payment-fields-container,
#panel-administracion form .payment-row {
  display: flex;
  flex-wrap: wrap;
  column-gap: 20px;
  row-gap: 25px;
}
#panel-administracion form .payment-row {
  row-gap: 15px;
}
#panel-administracion form .payment-row:not(:last-child) {
  margin-bottom: 40px;
}
#panel-administracion form .payment-row .remove-payment-row {
  position: absolute;
  color: red;
  top: -6px;
  right: 0;
  background-color: #fff;
  border-radius: 50%;
  line-height: 1;
  font-size: 9px;
  padding: 8px;
  border: 1px solid;
  text-decoration: none;
  transition: all 0.5s;
}
#panel-administracion form .payment-row .remove-payment-row:hover {
  background-color: red;
  color: #fff;
  border-color: red;
}
#panel-administracion .wrap-title {
  background-color: #0095e8;
  box-shadow: 0 0 7px 1px rgba(0, 0, 0, 0.32);
}
#panel-administracion .wrap-title .title {
  margin: 0;
  line-height: 1;
  color: #fff;
  padding: 15px 20px;
  margin-bottom: 20px;
  text-transform: uppercase;
  font-size: 22px;
  font-weight: 700;
  text-shadow: 0px 0px 4px rgba(0, 0, 0, 0.45);
}